

.border {
    width: 80%;
    border: 1px solid black;
    margin-left: 20%;
    padding: 20px;
    background-color: white;
    margin-top: 30px;
    outline: none;
    border: none;
}

.content {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    padding-left: 30px;
}
.content h4{
    align-items: center;
    gap: 10px;
}

.drag {
    width: 96%;
    height: 100px;
    border: 2px dotted blue;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.customer {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-left: 30px;
     
}
.customer h3{
    align-items: center;
}
.custom {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-evenly;
    flex-wrap: wrap;
    text-align: start;
    gap: 60px;
}
.custom label{
    line-height: 30px;
    align-items: flex-start;
}
.custom .input-group {
    display: flex;
    flex-direction: column;
    gap: 5px; 
    width: 150px; 
}

.product {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-left: 30px;
}
.product label{
    align-items: flex-start;
}

.products {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between;
    flex-wrap: wrap;
    text-align: start;
    gap:30px;
}


.products .input-group {
    display: flex;
    flex-direction: column;
    gap: 5px; 
    width: 150px;
}

button {
    background-color: blue;
    padding: 10px;
    outline: hidden;
    border: hidden;
    color: white;
    margin-top: 10px;
}

.apps{
    font-family: Arial, sans-serif;
    display: flex;
    flex-direction: column;
   flex-wrap: wrap;
   align-items: flex-start;
   text-align: start;
    margin-top: 50px;
    
}

table {
    margin: 0 auto;
    border-collapse: collapse;
    width: 95%;
    
}

th, td {
    padding: 10px;
    text-align: center;
}

th {
    background-color: #f2f2f2;
}
.discount{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-left: 30px;
}
.notes, .terms {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
    margin-top: 40px;
    margin-right: 30px;
}

.subtotal {
    display: flex;
    flex-direction: column;
    padding-left: 30px;
    margin-top: 20px;
    background-color:lavender;
    width: 600px;
    height: 300px;
}

.total {
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding: 10px;
}

.total div {
    display: flex;
    justify-content: space-between;
    width: 300px;
    align-items: center;
}
.actions {
    display: flex;
    gap: 10px;
    margin-top: 20px;
}
.sec{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap:20px
}
textarea{
    width: 400px;
}
.sections{
    display: flex;
    justify-content: space-around;
}