.header {
    display: flex;
    flex-direction: column;
    padding: 0 20px;
    border: 2px solid black;
    background-color: white;
    width: 100%;
    outline: none;
    border: none;
}

.heading {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.head {
    display: flex;
    align-items: center;
    gap: 20px;
    position: relative;
    padding-left: 20px;
}


  

.font {
    font-size: 10px;
    text-align: center;
    margin-top: 2px;
}
.sal{
    padding-left: 50px;
}
.menu {
    margin-top: 10px;
    display: flex;
    /* padding-right: 160px; */
    width: 240px;
    position: absolute;
    top:63px;
    left: 0px;
    line-height:35px;
    text-align: start;
    border: 1px solid black;
    height: 100%;
    background-color: white;
    outline: none;
    border: none;

}
.log img {
    width: 50px;
  }
  
  .email {
    display: flex;
    flex-direction: column;
  }
  
  .log {
    display: flex;
    justify-content: space-evenly;
    align-items: center; 
    gap: 5px;
  }
  
  .log p {
    margin: 0;
  }
  
.icons{
    gap: 10px;
    font-size: 20px;
    display: flex;
    align-items: center;
    text-align: center;

}
.icons p{
    font-family: 200;
    font-size: 10px;
}
.nav {
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.menus {
    position: relative;
    border: 5px solid blue;
    background-color: blue;
    color: white;
    padding: 10px;
    cursor: pointer;
    width: 150px;
    display: flex;
    gap: 10px;
    align-items: center;
}
.icon{
    font-size: 20px;
    font-weight: 200;
}
.down{
    display: flex;
    justify-content: space-evenly;
    margin-left: 50px;
}

.menus ul.dropdown {

    position: absolute;
    left: 0;
    top: 100%;
    padding: 10px;
    list-style: none;

}

/* .menus:hover ul.dropdown {
    display: block;
} */
.img {
    display: flex;
    flex-direction: column;
}
.img img{
    width: 100px;
    margin-top: 30%;

}
.men {
    margin-left: 10px;
}

.menu ul li {
    list-style: none;
    margin: 0;
    padding: 0;
    cursor: pointer;
}
